:root {
  --card-box-shadow: 0 8px 17px 2px rgba(0,0,0,0.14), 0 3px 14px 2px rgba(0,0,0,0.12), 0 5px 5px -3px rgba(0,0,0,0.2);
  --hover-box-shadow: 0 2px 2px 0 rgba(0,0,0,0.14), 0 3px 1px -2px rgba(0,0,0,0.12), 0 1px 5px 0 rgba(0,0,0,0.2);
}

body {
  font: normal 16px/150% Arial, Helvetica, sans-serif;
}

home {
  display: block;
  text-align: center;
}

.icon-group {
  padding: var(--universal-padding);
}

.icon-group.left {
  text-align: left;
}

.icon-group.right {
  text-align: right;
}

#node-aditionalinfo {
  width: 200px;
  resize: none;
}

.material-icons {
  cursor: pointer;
  opacity: 0.5;
  vertical-align: middle;
}

.material-icons:hover {
  opacity: 1;
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

.material-icons.medium {
  font-size: 1.5em;
}

.card .card-content {
  padding: 1em;
}

.card .card-action {
  padding: 0;
}

.input-field {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.modal .modal-content {
  padding-bottom: 0.5rem;
}

.modal .row {
  margin-bottom: 0.5rem;
}

.toast {
  height: 1.5em;
  opacity: 0.7;
}

.toast.error {
  background-color: red;
}

.root-node {
  text-align: left;
  padding-left: var(--universal-margin);
}

.node-link {
  display: inline-block;
}

.node-link:hover {
  text-decoration: underline;
}

.tree-node {
  line-height: 1;
}

.tree-node .node-content {
  cursor: pointer;
  margin: 0.5em;
}

.tree-node > .node-children {
  padding-left: 1.5em;
}

.tree-node.expanded .node-toggle {
  content: "";
  display: block;
  position: relative;
  width: 0;
  height: 0;
  vertical-align: middle;
  margin-top: -6px;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid black;
  cursor: pointer;
  margin-right: 0.5em;
  display: inline-block;
}

.tree-node.collapsed .node-toggle {
  content: "";
  display: block;
  position: relative;
  width: 0;
  height: 0;
  vertical-align: middle;
  margin-top: -6px;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-left: 6px solid black;
  margin-right: -6px;
  margin-right: 0.5em;
  cursor: pointer;
  display: inline-block;
}

.node-content.selected {
  font-weight: bold;
}

.tree-node.collapsed > .node-children {
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.3s ease;
}

.tree-node.collapsed > .node-children .node-content:before {
  height: 0;
}

